export const addReview = (token, answered, advice, rating, textComment) => (dispatch, _getState, api) => (
  api.post(`метод  api должен быть тут/${token}`, {answered, advice, rating, textComment})
    .then(() => {
      dispatch(redirectToRoute(``));
    })
    .catch(({response}) => {
      //dispatch(getReviewStatus(response));
      throw Error(`Ошибка добавления`);
    })
);
